.getstarted > h1 {
  font-family: var(--font-heading);
  font-weight: 700;
  font-size: 2rem;
}
@media (min-width: 40em) {
  .getstarted > h1 {
    margin-bottom: 1.875rem;
    font-size: 2.25rem;
  }
}
@media (min-width: 71.875em) {
  .getstarted > h1 {
    font-size: 2.75rem;
  }
}
.getstarted > p {
  margin-bottom: 3.75rem;
  line-height: 1.5;
}
@media (min-width: 40em) {
  .getstarted > p {
    max-width: 37.5rem;
    margin: 0 auto 3.75rem;
    font-size: 1.25rem;
  }
}
.getstarted > a {
  display: inline-block;
  padding: 1.125rem 3.125rem;
  border: 0.0625rem solid var(--pink);
  border-radius: 5rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--white);
  background-color: var(--pink);
  transition: all 300ms ease-in-out;
}
.getstarted > a:hover {
  border: 0.0625rem solid var(--pink);
  color: var(--pink);
  background-color: var(--white);
}
@media (min-width: 40em) {
  .getstarted > a {
    padding: 1.25rem 5rem;
    border-radius: 5rem;
    font-size: 1.375rem;
  }
}
.getstarted > span {
  display: block;
  font-size: 2.1875rem;
  font-family: var(--font-heading);
  font-weight: 700;
  margin-bottom: 1.875rem;
}
@media (min-width: 40em) {
  .getstarted > span {
    font-size: 1.875rem;
  }
}
@media (min-width: 71.875em) {
  .getstarted > span {
    font-size: 2.1875rem;
  }
}